import {
	basePath
} from '@/utils/index'
import api from "@/api/category"
import memberapi from "@/api/member"
import {
	mapState
} from 'vuex'
import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiBadge from "@/components/badge/badge"
import tuiNomore from "@/components/nomore/nomore"
import tuiButton from "@/components/button/button"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiBadge,
		tuiNomore,
		tuiButton,
		tuiTopDropdown,
		tuiBottomPopup,
		tuiNumberbox
	},
	data() {
		return {
			basePath,
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			productInfo: {
				img_url: []
			}, //产品详情
			/* banner: [
				"https://www.thorui.cn/img/product/11.jpg",
				"https://www.thorui.cn/img/product/2.png",
				"https://www.thorui.cn/img/product/33.jpg",
				"https://www.thorui.cn/img/product/4.png",
				"https://www.thorui.cn/img/product/55.jpg",
				"https://www.thorui.cn/img/product/6.png",
				"https://www.thorui.cn/img/product/7.jpg",
				"https://www.thorui.cn/img/product/8.jpg"
			], */
			bannerIndex: 0,
			topMenu: [{
				icon: "message",
				text: "消息",
				size: 26,
				badge: 3
			}, {
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, {
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, {
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 2
			}, {
				icon: "kefu",
				text: "客服小蜜",
				size: 26,
				badge: 0
			}, {
				icon: "feedback",
				text: "我要反馈",
				size: 23,
				badge: 0
			}, {
				icon: "share",
				text: "分享",
				size: 26,
				badge: 0
			}],
			menuShow: false,
			popupShow: false,
			value: 1,
			collected: false,
			shopCartNum: 0,
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'member_id']),
	onLoad: function(options) {
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		console.log(JSON.stringify(options.id))
		this.getProductShow(options.id)
		/* let shopCartList = uni.getStorageSync('shopCartList');
		if (shopCartList) {
			console.log("购物车哦"+JSON.stringify(shopCartList));
			this.shopCartNum=JSON.parse(shopCartList).length;
		} */
		setTimeout(() => {
			uni.getSystemInfo({
				success: (res) => {
					this.width = obj.left || res.windowWidth;
					this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
					this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
					this.scrollH = res.windowWidth;

				}
			})

		}, 50)
	},
	methods: {
		//获取产品详情
		getProductShow(id) {
			var param = {
				member_id: this.member_id,
			}
			var url = api.getProductShow + '/' + id
			this.$postajax(url, param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						this.productInfo = res.data;
						this.productInfo.img_url = JSON.parse(res.data.img_url);
						this.collected = this.productInfo.follow == 0 ? false : true;
						console.log("图片数组" + JSON.stringify(res.data))
					}


				})
				.catch(err => {

				});
		},
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			console.log(JSON.stringify(e.currentTarget.dataset))
			let index = e.currentTarget.dataset.index;
			var imgurl = this.productInfo.img_url.map(item => basePath + item)
			uni.previewImage({
				current: index,
				urls: imgurl
			})
		},
		back: function() {
			uni.navigateBack()
		},
		openMenu: function() {
			this.menuShow = true
		},
		closeMenu: function() {
			this.menuShow = false
		},
		//跳转购物车
		shopCart() {
			uni.navigateTo({
				url: '../shopcart/shopcart'
			})
		},
		//打开添加购物车弹框
		showPopup: function() {
			this.popupShow = true
		},
		//确定加入购物车
		surePopup(){
			let shopCartArr = uni.getStorageSync('shopCartList');
			let shopCartList = [];
			this.productInfo.qyt = this.value;
			if (shopCartArr) {
				shopCartList = JSON.parse(shopCartArr);
			}
			if (shopCartArr && shopCartList.length > 0) {
				shopCartList.forEach(item => {
					if (this.productInfo.id == item.id) {
						item.qyt = item.qyt + this.productInfo.qyt;
						item.follow = this.productInfo.follow;
						item.follow_ = this.productInfo.follow_;
					} else {
						shopCartList.push(this.productInfo)
					}
			
				})
			} else {
				shopCartList.push(this.productInfo)
			}
			
			
			//console.log("产品数据"+JSON.stringify(this.productInfo))
			console.log("产品数量" + JSON.stringify(shopCartList))
			var $me = this;
			uni.setStorage({
				key: 'shopCartList',
				data: JSON.stringify(shopCartList),
				success: function(res) {
					$me.tui.toast("加入购物车成功", 2000, true);
					$me.shopCartNum = shopCartList.length;
				}
			});
			this.popupShow = false
		},
		//关闭弹框
		hidePopup: function() {				
			this.popupShow = false
		},
		change: function(e) {
			this.value = e.value
		},
		collecting: function() {
			this.collected = !this.collected;
			if (this.collected) {
				var param = {
					member_id: this.member_id,
					pid: this.productInfo.id
				}
				this.$postajax(memberapi.addFollow, param)
					.then(res => {
						console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.tui.toast("收藏成功", 2000, true);
						}
					})
					.catch(err => {

					});
			} else {
				var url = memberapi.closeFollow + '/' + this.productInfo.id;
				this.$postajax(url)
					.then(res => {
						console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.tui.toast("取消收藏成功", 2000, true);
						}


					})
					.catch(err => {

					});
			}


		},
		common: function() {
			this.tui.toast("功能开发中~")
		},
		chat() {
			uni.navigateTo({
				url: '../chat/chat'
			})
		},
		submit() {
			this.popupShow = false
			console.log(JSON.stringify(this.productInfo.id))
			uni.navigateTo({
				url: '../submitOrder/submitOrder?id=' + this.productInfo.id + '&qyt=' + this.value
			})
		},
		coupon() {
			uni.navigateTo({
				url: '../extend-view/coupon/coupon'
			})
		}
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
